.nav-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  background-color: #191536;
  width: 100%;
  height: 100vh;
  padding: 20px 0;
  z-index: 1;
  transform: translateX(100%);
  transition: all 0.3s;

  &:target {
    transform: translateX(0);
  }

  &__cross {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 50px;

    &-link {
      display: flex;
      background-image: url(../images/nav-page/cross.svg);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 17px;
      height: 17px;
    }
  }

  &__list {
    list-style: none;
    padding: 0;
  }

  &__item {
    padding: 11px 0;
  }

  &__link {
    font-size: 21px;
    line-height: 130%;
    text-decoration: none;
    color: #fff;

    &:hover {
      transition: color 0.3s;
      color: $color-blue;
    }
  }
}

@include onDesctop-768 {
  .nav-page {
    padding: 34px 0;

    &__cross {
      margin-bottom: 56px;
    }
  }
}
